// @flow

import * as React from 'react';

import { MobileBar, LinkButton } from './styled';

import Icon from 'components/Icons';

const $silverFern500 = '#666';

type Props = {
  mobilePanelSwitch: Function,
};

class MobileBarComponent extends React.Component<Props> {
  openMobilePanel = (e: SyntheticEvent<HTMLButtonElement>) => {
    this.props.mobilePanelSwitch();
    e.stopPropagation();
  };

  render = () => {
    return (
      <MobileBar>
        <nav>
          <LinkButton onClick={this.openMobilePanel}>
            <Icon icon="edit-hamburger-menu" size="30" color={$silverFern500} />
          </LinkButton>
          <h3>Biometric Identification</h3>
          <div />
        </nav>
      </MobileBar>
    );
  };
}

export default MobileBarComponent;
