import styled, { injectGlobal } from 'styled-components';

import {
  grtMediumViewport,
  mainFontFamily,
  textBaseColor,
  backgroundColor,
} from 'styles/helpers';

/* eslint-disable */
injectGlobal`
  html, body {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
    background: ${backgroundColor};
    font-size: 14px;
    @media ${grtMediumViewport} {
      font-size: 16px;
    }
    font-family: ${mainFontFamily};
    color: ${textBaseColor};
  }
  html {
    /* Avoid scrollbar space moving when showing mask
      it scrolls up to the view so no cool
    */
    // overflow-y: scroll;
  }

  * {
    box-sizing: border-box;
  }
`;
/* eslint-enable */

export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 100vh;
`;

export const Container = styled.div`
  flex: 1 1 auto;
`;

export const PanelMask = styled.div`
  position: fixed;
  z-index: 100;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.5s, visibility 0.5s;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: black;
  ${({ active }) =>
    active &&
    `
    visibility: visible;
    opacity: .4;
    transition: opacity 0.5s, visibility 0.5s;
  `};
`;
