// @flow

import * as React from 'react';

import { Option, OptionWrapper } from './styled';
import Toogle from 'components/Toogle';

type ToogleOptionProps = {
  label: string,
  on: boolean,
  enabled?: boolean,
  onClick: Function,
};

export const ToogleOption = ({
  label,
  on,
  onClick,
  enabled = true,
}: ToogleOptionProps) => (
  <Option>
    <OptionWrapper>
      {label}
      <Toogle on={on} onClick={onClick} enabled={enabled} />
    </OptionWrapper>
  </Option>
);

type OptionsGroupOptionProps = {
  label: string,
  options: Array<any>,
  name: string,
  selected: string,
  onChange: Function,
};

export const OptionsGroupOption = ({
  label,
  options,
  name,
  selected,
  onChange,
}: OptionsGroupOptionProps) => (
  <Option>
    <OptionWrapper>
      <div>{label}</div>
      <div>
        <select id={name} onChange={onChange} value={selected}>
          {options.map((value, idx) => (
            <option key={idx} value={value}>
              {value}
            </option>
          ))}
        </select>
      </div>
    </OptionWrapper>
  </Option>
);

type RangeOptionProps = {
  label: string,
  value: string,
  min: string,
  max: string,
  onChange: Function,
};

export const RangeOption = ({
  label,
  value,
  min,
  max,
  onChange,
}: RangeOptionProps) => (
  <Option>
    <OptionWrapper>
      <div>{label}</div>
      <input
        type="range"
        min={min}
        max={max}
        value={value}
        onChange={onChange}
        className="slider"
      />
    </OptionWrapper>
  </Option>
);
