import styled, { keyframes } from 'styled-components';

export const Container = styled.div`
  visibility: ${({ mediaActivated }) =>
    mediaActivated ? 'visible' : 'hidden'};
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: black;
`;

export const VideoContainer = styled.div`
  position: relative;
  height: 100%;
  max-height: 100%;
`;

export const Title = styled.div`
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 30px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
  color: white;
  padding: 0.2rem;
  background: ${({ speechRecognised }) =>
    speechRecognised ? 'rgba(32, 205, 32, 0.5)' : 'rgba(200, 200, 200, 0.5)'};
  ${({ speechRecognised }) =>
    speechRecognised ? `animation: ${blink} 0.3s 5 alternate;` : ''};
`;

const blink = keyframes`
  from {
    background-color: darkgreen;
  }
  to {
    background-color: lightgreen;
  }
`;

export const OverlayContainer = styled.div`
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const OverlayContent = styled.div`
  position: relative;
  top: -${({ overlaysize: { correction } }) => correction}px;
  height: ${({ overlaysize: { height, width }, rotate }) =>
    rotate === 'true' && width > height ? width : height}px;
  width: ${({ overlaysize: { height, width }, rotate }) =>
    rotate === 'true' && width > height ? height : width}px;
  ${({ overlaysize: { height, width }, rotate }) =>
    rotate === 'true' && width > height ? 'transform: rotate(-90deg);' : ''};
  padding: 0.5rem 0.5rem;
  transition: transform 0.5s;
`;

export const ProgressBar = styled.progress`
  position: absolute;
  bottom: 52px;
  left: 0px;
  width: 100%;
  height: 1px;
  appearance: none;
  margin: 0 0 0.7rem 0;

  ::-webkit-progress-bar {
    background-color: white;
  }
  ::-webkit-progress-value {
    background-color: red;
  }
  ::-moz-progress-bar {
    background-color: white;
  }
`;

const pulse = keyframes`
  from {
    background-color: darkred;
  }
  to {
    background-color: lightred;
  }
`;

export const RecButton = styled.div`
  position: absolute;
  bottom: 8px;
  left: calc(50% - 25px);
  z-index: 100;
  width: 50px;
  height: 50px;
  min-height: 50px;
  border-radius: 50px;
  background: rgba(100, 100, 100, .5);
  :hover {
    cursor: pointer;
  }
  :after {
    content: '';
    background: red;
    width: 20px;
    height: 20px;
    border-radius: 50px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto auto;
    ${({ recording }) =>
      recording ? `animation: ${pulse} 0.5s infinite alternate;` : ''}
`;
