// @flow

import { REQUEST_SESSIONS } from './constants';
import { GET_SESSIONS } from 'web-api';
import API from 'util/api-interface';

export const requestSessions = () => dispatch =>
  API(GET_SESSIONS, '', {}).then(
    json => dispatch({ type: REQUEST_SESSIONS, json: json.data }),
    err => {
      throw new Error(err);
    }
  );
