// @flow

import * as React from 'react';
import { connect } from 'react-redux';

import { saveVideo } from '../actions';
import Step from './Step';
import { CallToAction1, CallToAction2, CallToAction3 } from './styled';

type StepProps = {
  sessionId: string,
  saveVideo: Function,
  recordingTime: string,
  speechRecognition: boolean,
  autoStartOnVoiceCommand: boolean,
  speechRecognition: boolean,
  videoOptions: VideoOptions,
  resolution: Array<string>,
  idType?: string,
};

const Step1Component = (props: StepProps) => (
  <Step
    videoOptions={{
      ...props.videoOptions,
      constrains: {
        audio: false,
        video: {
          facingMode: 'environment',
          width: props.resolution[0],
          height: props.resolution[1],
        },
      },
    }}
    mediaCaptureTxt="Align passport photo page to boundaries for 5 seconds."
    CallToAction={CallToAction1}
    stepTitleTxt="New Zealand passport capture"
    stepBodyTxt="Align passport photo page to boundaries for 5 seconds."
    stepNumber={1}
    progressBarInitialValue={0}
    progressBarStepValue={34}
    completedTxt="Passport capture complete"
    nextRoute="/biometric-id/step2"
    redoTxt="Redo passport capture"
    svgOverlay={props.idType}
    speechRecognition={false}
    autoStartOnVoiceCommand={props.autoStartOnVoiceCommand}
    recordingTime={+props.recordingTime * 1000}
    sessionId={props.sessionId}
    saveVideo={props.saveVideo}
  />
);

const Step2Component = (props: StepProps) => (
  <Step
    videoOptions={{
      ...props.videoOptions,
      constrains: {
        audio: false,
        video: {
          facingMode: 'user',
          width: props.resolution[0],
          height: props.resolution[1],
        },
      },
    }}
    mediaCaptureTxt="Hold passport photo page next to your face for 5 seconds."
    CallToAction={CallToAction2}
    stepTitleTxt="Face &amp; Passport"
    stepBodyTxt="Place Passport photo page open next to your head and tilt the passport back and forward slightly."
    stepNumber={2}
    progressBarInitialValue={34}
    progressBarStepValue={66}
    completedTxt="Face &amp; passport capture complete"
    nextRoute="/biometric-id/step3"
    redoTxt="Redo face and passport capture"
    speechRecognition={false}
    autoStartOnVoiceCommand={props.autoStartOnVoiceCommand}
    recordingTime={+props.recordingTime * 1000}
    sessionId={props.sessionId}
    saveVideo={props.saveVideo}
  />
);

const Step3Component = (props: StepProps) => (
  <Step
    videoOptions={{
      ...props.videoOptions,
      constrains: {
        audio: false,
        video: {
          facingMode: 'user',
          width: props.resolution[0],
          height: props.resolution[1],
        },
      },
    }}
    mediaCaptureTxt="&quot;I want to open a BNZ account.&quot;"
    CallToAction={CallToAction3}
    stepTitleTxt="Repeat a simple phrase"
    stepBodyTxt="Clearly repeat the words shown to you on the top of the next screen."
    stepNumber={3}
    progressBarInitialValue={66}
    progressBarStepValue={100}
    completedTxt="Repeat a simple phrase completed"
    nextRoute="/biometric-id/end"
    redoTxt="Redo Repeat a simple phrase"
    speechRecognition={props.speechRecognition}
    autoStartOnVoiceCommand={props.autoStartOnVoiceCommand}
    recordingTime={+props.recordingTime * 1000}
    sessionId={props.sessionId}
    saveVideo={props.saveVideo}
  />
);

const mapStateToProps = ({
  biometricId: {
    sessionId,
    settings: {
      autoStartOnDetection,
      captureVideo,
      videoQuality,
      framesPerSecond,
      recordingTime,
      faceDetection,
      faceAngleDetection,
      speechRecognition,
      autoStartOnVoiceCommand,
      imageType,
      idType,
      resolution,
      useMediaRecorderApi,
      overlayAutoRotation,
    },
  },
}) => ({
  sessionId,
  recordingTime,
  idType,
  speechRecognition,
  autoStartOnVoiceCommand,
  videoOptions: {
    autoStartOnDetection,
    recordEnabled: captureVideo,
    faceDetection,
    faceAngleDetection,
    useMediaRecorderApi,
    fps: +framesPerSecond,
    quality: +videoQuality,
    imageType,
    overlayAutoRotation,
  },
  resolution: resolution.split('x'),
});

export const Step1 = connect(mapStateToProps, { saveVideo })(Step1Component);
export const Step2 = connect(mapStateToProps, { saveVideo })(Step2Component);
export const Step3 = connect(mapStateToProps, { saveVideo })(Step3Component);
