// @flow
import * as React from 'react';

import { Toogle, Switch } from './styled';

type Props = {
  on: boolean,
  onClick: Function,
  enabled: boolean,
};

class ToogleComponent extends React.Component<Props> {
  static defaultProps = {
    on: true,
    onClick: () => {},
    enabled: true,
  };

  constructor(props: Props) {
    super(props);
  }

  handleOnClick = () => this.props.enabled && this.props.onClick();

  render() {
    const { on, enabled } = this.props;
    return (
      <Toogle on={on} enabled={enabled} onClick={this.handleOnClick}>
        <Switch on={on} enabled={enabled} />
      </Toogle>
    );
  }
}

export default ToogleComponent;
