// import { handle } from 'redux-pack';

import * as types from '../actions/action-types';

// APP REDUCER
export function app(state = {}, payload) {
  const newState = { ...state };

  switch (payload.type) {
    case types.MOBILE_PANEL_SWITCH: {
      newState.ui.mobilePanel = !newState.ui.mobilePanel;
      break;
    }
  }
  return newState;
}
