// @flow

import * as React from 'react';

import { MobileMenu, Header, CloseButton, Options, Option } from './styled';

import { tipTop500 } from 'styles/helpers';

import Icon from 'components/Icons';
import SidePanel from 'components/SidePanel';

const options = [
  { icon: 'perm-identity', label: 'Home', url: '/' },
  { icon: 'camera-front', label: 'Biometric ID', url: '/biometric-id' },
  { icon: 'video-library', label: 'Review tool', url: '/sessions-review' },
  { icon: 'settings', label: 'Settings', url: '/settings' },
];

type Props = {
  mobilePanelSwitch: Function,
  mobilePanel: boolean,
};

class MobilePanel extends React.Component<Props> {
  componentWillUpdate({ mobilePanel }: Props) {
    const body = document.body;
    if (body) {
      body.className = mobilePanel ? 'active_panel' : '';
      if (mobilePanel) {
        (document.addEventListener: Function)(
          'click',
          this.clickHandler,
          false
        );
      } else {
        (document.removeEventListener: Function)(
          'click',
          this.clickHandler,
          false
        );
      }
    }
  }

  shouldComponentUpdate = (next: Props) =>
    this.props.mobilePanel !== next.mobilePanel;

  clickHandler = (e: any) => {
    const mobBar = document.querySelector('#mob-bar');
    if (mobBar && !mobBar.contains(e.target)) {
      this.props.mobilePanelSwitch();
    }
  };

  render() {
    const { mobilePanel, mobilePanelSwitch } = this.props;
    return (
      <aside>
        <SidePanel active={mobilePanel}>
          <MobileMenu id="mob-bar">
            <Header>
              <CloseButton onClick={mobilePanelSwitch}>
                <Icon icon="close" size="40" color={tipTop500} />
              </CloseButton>
            </Header>
            <Options>{renderOptions(options, mobilePanelSwitch)}</Options>
          </MobileMenu>
        </SidePanel>
      </aside>
    );
  }
}

const renderOptions = (options, mobilePanelSwitch) =>
  options.map(({ icon, label, url }, idx) => (
    <Option to={url} key={idx} onClick={mobilePanelSwitch}>
      <Icon icon={icon} size="24" color={tipTop500} />
      <span>{label}</span>
    </Option>
  ));

export default MobilePanel;
