import * as types from './action-types';
import { DUMMY } from 'web-api';
import API from 'util/api-interface';

export const mobilePanelSwitch = () => ({
  type: types.MOBILE_PANEL_SWITCH,
});

export const storeActiveURL = payload => ({
  type: types.STORE_ACTIVE_ROUTE,
  payload,
});

export const loadDummy = () => ({
  type: types.LOAD_DUMMY,
  promise: API(DUMMY, 'dummyfile'),
});
