// @flow
import * as React from 'react';

import { ProgressBarStyled } from './styled';

type Props = {
  style?: string,
  value: number,
  border?: number,
  color?: string,
  background?: string,
  bgOpacity?: number,
  timeTransform?: number,
};
type State = {
  viewBox: string,
  strokeDasharray: string,
  percent: number,
  speedTransform: number,
};

class ProgressBar extends React.Component<Props, State> {
  static defaultProps = {
    style: 'lineal',
    value: 0,
    border: 5,
    color: '#0f0',
    background: '#ccc',
    bgOpacity: 1,
    timeTransform: 0,
  };
  viewBoxSize = 235;
  state: State = {
    viewBox: `0 0 ${this.viewBoxSize} ${this.viewBoxSize}`,
    strokeDasharray: '0 725',
    percent: 0,
    speedTransform: 0,
  };

  meter: any; // No Flow type for SVGs

  constructor(props: Props) {
    super(props);
  }

  componentDidMount() {
    this.setBorder();
    this.updateMeter();
  }

  componentDidUpdate(prevProps: Props) {
    if (prevProps.value !== this.props.value) this.updateMeter();
  }

  setBorder = () => {
    // If border adjust SVG viewBox
    const { border, style } = this.props;
    const offset = parseInt(border, 10);
    const height = style === 'lineal' ? offset : this.viewBoxSize + offset;
    if (this.props.border) {
      this.setState({
        viewBox: `-${offset / 2} -${offset / 2} ${this.viewBoxSize +
          offset} ${height}`,
      });
    }
  };

  updateMeter = () => {
    if (this.meter) {
      const value =
        this.props.value > 100
          ? 100
          : this.props.value < 0
            ? 0
            : this.props.value;
      const length = this.meter.getTotalLength();
      const newPercent = value * length / 100;
      let speed;
      if (this.props.timeTransform) {
        const fixedIncrement = 0.3;
        speed =
          (newPercent - this.state.percent) *
            this.props.timeTransform /
            length +
          fixedIncrement;
      } else {
        speed = 0;
      }
      this.setState({
        strokeDasharray: `${newPercent} ${length}`,
        percent: newPercent,
        speedTransform: speed,
      });
    }
  };

  render() {
    const { style, border, color, background, bgOpacity } = this.props;
    const { viewBox, speedTransform, strokeDasharray } = this.state;
    return (
      <ProgressBarStyled
        viewBox={viewBox}
        border={border}
        speed={speedTransform}
      >
        <path
          stroke={background}
          d={
            style === 'lineal'
              ? `M0 0 L${this.viewBoxSize} 0`
              : 'M117.06,2A115.06,115.06,0,1,1,2,117.06,115.19,115.19,0,0,1,117.06,2Z'
          }
          fill="none"
          strokeOpacity={bgOpacity}
        />
        <path
          ref={meter => {
            this.meter = meter;
          }}
          stroke={color}
          d={
            style === 'lineal'
              ? `M0 0 L${this.viewBoxSize} 0`
              : 'M117.06,2A115.06,115.06,0,1,1,2,117.06,115.19,115.19,0,0,1,117.06,2Z'
          }
          fill="none"
          strokeDasharray={strokeDasharray}
        />
      </ProgressBarStyled>
    );
  }
}

export default ProgressBar;
