// @flow
import * as React from 'react';
import { connect } from 'react-redux';

import { switchSetting, setSetting } from 'app/BiometricID/actions';
import { ToogleOption, OptionsGroupOption, RangeOption } from './options';
import { Container, Content, Title } from './styled';

type Props = {
  autoStartOnDetection: boolean,
  autoStartOnVoiceCommand: boolean,
  switchSetting: Function,
  setSetting: Function,
  framesPerSecond: string,
  captureVideo: boolean,
  recordingTime: string,
  speechRecognition: boolean,
  useMediaRecorderApi: boolean,
  faceAngleDetection: boolean,
  faceDetection: boolean,
  overlayAutoRotation: boolean,
  imageType: string,
  idType: string,
  resolution: string,
  videoQuality: string,
  videoCodec: string,
};

type State = {
  cameraResolutions: Array<string>,
};

export class Settings extends React.Component<Props, State> {
  state = { cameraResolutions: [] };

  constructor(props: Props) {
    super(props);
    // TODO: move somewhere else
    this.detectVideoSizes();
  }

  detectVideoSizes = (idx = 0) => {
    const cstList = [
      { w: 1600, h: 1200 },
      { w: 1280, h: 720 },
      { w: 640, h: 480 },
      { w: 320, h: 240 },
    ];
    navigator.mediaDevices
      .getUserMedia({
        video: {
          width: { exact: cstList[idx].w },
          height: { exact: cstList[idx].h },
        },
      })
      .then(stream => {
        stream.getTracks().forEach(track => track.stop());
        this.setState({
          cameraResolutions: [
            ...this.state.cameraResolutions,
            `${cstList[idx].w}x${cstList[idx].h}`,
          ],
        });
        if (cstList[idx + 1]) this.detectVideoSizes(idx + 1);
      })
      .catch(() => {
        if (cstList[idx + 1]) this.detectVideoSizes(idx + 1);
      });
  };

  isMediaRecorderEnabled = () => !!window.MediaRecorder;

  isSpeechRecognitionEnabled = () =>
    !!(
      window.SpeechRecognition ||
      window.webkitSpeechRecognition ||
      window.mozSpeechRecognition ||
      window.msSpeechRecognition ||
      window.oSpeechRecognition
    );

  render = () => {
    const {
      autoStartOnDetection,
      autoStartOnVoiceCommand,
      switchSetting,
      setSetting,
      captureVideo,
      recordingTime,
      speechRecognition,
      useMediaRecorderApi,
      faceAngleDetection,
      faceDetection,
      overlayAutoRotation,
      framesPerSecond,
      imageType,
      idType,
      resolution,
      videoQuality,
      videoCodec,
    } = this.props;
    return (
      <Container>
        <Content>
          <Title>Settings</Title>

          <h3 style={{ marginBottom: '0' }}>Video</h3>
          <ToogleOption
            label="Capture video"
            on={captureVideo}
            onClick={() => switchSetting('captureVideo')}
          />
          <OptionsGroupOption
            label="Video codec"
            name="video-codec"
            onChange={e => setSetting('videoCodec', e.target.value)}
            selected={videoCodec}
            options={['H.264', 'VP8']}
          />
          <OptionsGroupOption
            label="Key frames format"
            name="image-format"
            onChange={e => setSetting('imageType', e.target.value)}
            selected={imageType}
            options={['jpeg', 'png']}
          />
          <OptionsGroupOption
            label="Resolution"
            name="resolution"
            onChange={e => setSetting('resolution', e.target.value)}
            selected={resolution}
            options={this.state.cameraResolutions}
          />
          <RangeOption
            label={`Quality ${videoQuality}%`}
            value={videoQuality}
            min="1"
            max="100"
            onChange={e => setSetting('videoQuality', e.target.value)}
          />
          <RangeOption
            label={`Frames per second ${framesPerSecond}fps`}
            value={framesPerSecond}
            min="5"
            max="30"
            onChange={e => setSetting('framesPerSecond', e.target.value)}
          />
          <RangeOption
            label={`Recording time ${recordingTime}s`}
            value={recordingTime}
            min="1"
            max="10"
            onChange={e => setSetting('recordingTime', e.target.value)}
          />
          <ToogleOption
            label={`Use MediaRecorder API. ${
              this.isMediaRecorderEnabled() ? '' : '(not supported)'
            }`}
            enabled={this.isMediaRecorderEnabled()}
            on={this.isMediaRecorderEnabled() && useMediaRecorderApi}
            onClick={() => switchSetting('useMediaRecorderApi')}
          />

          <h3 style={{ marginBottom: '0' }}>Biometric Identification</h3>
          <OptionsGroupOption
            label="Id type"
            name="idType"
            onChange={e => setSetting('idType', e.target.value)}
            selected={idType}
            options={['Passport', 'Driver License']}
          />
          <ToogleOption
            label="Face detection and tracking"
            on={faceDetection}
            onClick={() => switchSetting('faceDetection')}
          />
          <ToogleOption
            label="Face angle detection"
            on={faceAngleDetection}
            onClick={() => switchSetting('faceAngleDetection')}
          />
          <ToogleOption
            label="Start recording on face detection (experimental)"
            on={autoStartOnDetection}
            onClick={() => switchSetting('autoStartOnDetection')}
          />
          <ToogleOption
            label={`Start recording on voice command. ${
              this.isSpeechRecognitionEnabled()
                ? 'Say "Start"'
                : '(not supported)'
            }`}
            enabled={this.isSpeechRecognitionEnabled()}
            on={autoStartOnVoiceCommand}
            onClick={() => switchSetting('autoStartOnVoiceCommand')}
          />
          <ToogleOption
            label={`Speech recognition on Step 3. ${
              this.isSpeechRecognitionEnabled() ? '' : '(not supported)'
            }`}
            enabled={this.isSpeechRecognitionEnabled()}
            on={speechRecognition}
            onClick={() => switchSetting('speechRecognition')}
          />

          <h3 style={{ marginBottom: '0' }}>Miscellaneous</h3>
          <ToogleOption
            label="Fully responsive (auto rotate)"
            on={overlayAutoRotation}
            onClick={() => switchSetting('overlayAutoRotation')}
          />
        </Content>
      </Container>
    );
  };
}

const mapStateToProps = ({
  biometricId: {
    settings: {
      autoStartOnDetection,
      autoStartOnVoiceCommand,
      captureVideo,
      recordingTime,
      speechRecognition,
      useMediaRecorderApi,
      faceAngleDetection,
      faceDetection,
      overlayAutoRotation,
      framesPerSecond,
      imageType,
      idType,
      resolution,
      videoQuality,
      videoCodec,
    },
  },
}) => ({
  autoStartOnDetection,
  autoStartOnVoiceCommand,
  captureVideo,
  recordingTime,
  speechRecognition,
  useMediaRecorderApi,
  faceAngleDetection,
  faceDetection,
  overlayAutoRotation,
  framesPerSecond,
  imageType,
  idType,
  resolution,
  videoQuality,
  videoCodec,
});

export default connect(mapStateToProps, {
  switchSetting,
  setSetting,
})(Settings);
