const baseUrl = '/api/';

// DUMMY
let root = `${baseUrl}v1/dummy/`;
export const DUMMY = { url: `${root}##.json?`, mth: 'GET' };

// BIOMETRIC ID
root = `${baseUrl}v1/biometric-id/`;
export const BIO_ID_FACE_DETECTION = {
  url: `${root}face-detection/##.json?`,
  mth: 'POST',
};
export const BIO_ID_SAVE_VIDEO = {
  url: `${root}save-video`,
  mth: 'POST',
};
export const SAVE_VIDEO_IMAGES = {
  url: `${root}save-video-images`,
  mth: 'POST',
};
export const GET_SESSIONS = {
  url: `${root}sessions.json?`,
  mth: 'GET',
};

/*
  *** API QUERY IMPLEMENTATION ***
*/

const parseQueryParams = obj =>
  Object.keys(obj).reduce(
    (acc, key, idx) =>
      `${acc}${idx ? '&' : ''}${encodeURIComponent(key)}=${encodeURIComponent(
        obj[key]
      )}`,
    ''
  );

const setURLParams = (endPoint, URLParams) =>
  endPoint.url.replace('##', URLParams);

const composeEndPoint = (endPoint, URLParams, queryParams) =>
  `${setURLParams(endPoint, URLParams)}${parseQueryParams(queryParams)}`;

const init = ({ mth, auth }, data) => {
  const JWT = localStorage.getItem('trademe.AuthToken');
  return Object.assign(
    {},
    {
      method: mth,
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json',
      },
      mode: 'cors',
    },
    data ? { body: JSON.stringify(data) } : {},
    auth && JWT ? { headers: { authorization: `Bearer ${JWT}` } } : {}
  );
};

const API = (endPoint, URLParams = '', queryParams = {}, data) =>
  fetch(
    composeEndPoint(endPoint, URLParams, queryParams),
    init(endPoint, data)
  );

export default API;
