// @flow
import * as React from 'react';

type IconProps = {
  icon: string,
  size?: string,
  color?: string,
};

const Icon = ({ icon, size, color, ...props }: IconProps) => {
  const style = color ? { fill: color } : {};
  return (
    <svg
      height={size}
      width={size}
      role="img"
      aria-label={`${icon} Icon`}
      {...props}
    >
      <use xlinkHref={`/assets/images/svgs.svg#${icon}`} style={style} />
    </svg>
  );
};

Icon.defaultProps = {
  size: 25,
};

type LogoSize = {
  w: number,
  h: number,
};

const sizes = {
  full: { w: 49, h: 24 },
};

const calcWitdh = (logSize: LogoSize, svgHeight: number = 25) =>
  Math.round(logSize.w * svgHeight / logSize.h);

type LogoProps = {
  logo: string,
  size?: number,
  color?: string,
  class?: string,
};

export const Logo = ({ logo, size, color, ...props }: LogoProps) => {
  const style = color ? { fill: color } : {};
  return (
    <svg
      width={calcWitdh(sizes[logo], size)}
      height={size}
      className={props.class}
      role="img"
      aria-label="Logo"
    >
      <use xlinkHref={`./assets/images/svgs.svg#${logo}`} style={style} />
    </svg>
  );
};

Logo.defaultProps = {
  logo: 'full',
  size: 25,
};

export default Icon;
