import { createStore, applyMiddleware, compose } from 'redux';
import thunk from 'redux-thunk';
import { save, load } from 'redux-localstorage-simple';

import reducer from './reducers';

const initialState = {
  app: {
    ui: {
      mobilePanel: false,
    },
  },
  biometricId: {
    sessionId: '',
    settings: {
      autoStartOnDetection: false,
      autoStartOnVoiceCommand: false,
      captureVideo: false,
      speechRecognition: false,
      useMediaRecorderApi: true,
      faceAngleDetection: false,
      faceDetection: false,
      overlayAutoRotation: false,
      framesPerSecond: '24',
      recordingTime: '5',
      imageType: 'jpeg',
      idType: 'Passport',
      resolution: '640x480',
      videoQuality: '50',
      videoCodec: 'h264',
    },
  },
  sessions: { list: [] },
};
let store;

const Store = {
  configureStore: () => {
    const middleware = applyMiddleware(save(), thunk);
    if (process.env.NODE_ENV === 'development') {
      const composeEnhancers =
        window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
      const enhancer = composeEnhancers(middleware);
      store = createStore(reducer, load(), enhancer);
    } else {
      store = createStore(reducer, initialState, middleware);
    }
    return store;
  },
  getState: function() {
    return store.getState();
  },
  getInitialState: function() {
    return initialState;
  },
  dispatch: function(action) {
    store.dispatch(action);
  },
};

export { Store as default };
