// @flow

import * as React from 'react';
import { connect } from 'react-redux';

import {
  Container,
  Content,
  SessionWrapper,
  Title,
  SessionId,
  VideosWrapper,
  Video,
} from './styled';
import * as appActions from './actions';

type Props = {
  requestSessions: Function,
  sessions: Array<any>,
};

class SessionsReview extends React.Component<Props> {
  socket: Socket;

  constructor(props) {
    super(props);
  }
  componentDidMount() {
    this.socket = new WebSocket('ws://localhost:8082');
    this.socket.addEventListener('open', () => {
      this.socket.send('Hello Server!');
      this.socket.onmessage = this.socketMsg;
    });
    this.props.requestSessions();
  }

  componentWillUnmount() {
    this.socket.close();
  }

  socketMsg = event => {
    log('Message from server ', event.data);
    this.props.requestSessions();
  };

  render = () => (
    <Container>
      <Content>
        <Title>Review tool</Title>
        {this.props.sessions
          ? this.props.sessions.list.map((session, idx) => (
              <SessionWrapper key={idx}>
                <SessionId>{session.id}</SessionId>
                <VideosWrapper>
                  {session.step1 ? (
                    <Video
                      src={`/videos/${session.id}/step1.${
                        session.encapsulation
                      }`}
                      type={`video/${session.encapsulation}`}
                      controls
                    />
                  ) : null}
                  {session.step2 ? (
                    <Video
                      src={`/videos/${session.id}/step2.${
                        session.encapsulation
                      }`}
                      type={`video/${session.encapsulation}`}
                      controls
                    />
                  ) : null}
                  {session.step3 ? (
                    <Video
                      src={`/videos/${session.id}/step3.${
                        session.encapsulation
                      }`}
                      type={`video/${session.encapsulation}`}
                      controls
                    />
                  ) : null}
                </VideosWrapper>
              </SessionWrapper>
            ))
          : null}
      </Content>
    </Container>
  );
}

const mapStateToProps = ({ sessions }) => ({
  sessions,
});

export default connect(mapStateToProps, {
  ...appActions,
})(SessionsReview);
