import * as types from './constants';

// BIOMETRIC ID REDUCER
export function biometricId(state = {}, payload) {
  const newState = { ...state };

  switch (payload.type) {
    case types.FACE_DETECTION: {
      newState.faceDetection.coordinates = [...payload.json];
      break;
    }

    case types.INIT_BIOMETRIC_ID_SESSION: {
      newState.sessionId = payload.id;
      break;
    }

    case types.SAVE_VIDEO: {
      // TODO: do something with the video Blob or Array of frames
      // newState.sessionId = payload.id;
      break;
    }

    case types.SWITCH_SETTING: {
      newState.settings[payload.setting] = !state.settings[payload.setting];
      break;
    }

    case types.SET_SETTING: {
      newState.settings[payload.setting] = payload.value;
      break;
    }
  }
  return newState;
}
