// @flow

import * as React from 'react';
import debounce from 'lodash-es/debounce';

import HT from 'util/headtracker';
import Annyang from 'util/annyang';
import { toggleFullScreen } from 'util/helpers';
import Recorder from './recorder';
import ProgressBar from 'components/ProgressBar';
import IdOverlay from 'components/IdOverlay';
import {
  Container,
  VideoContainer,
  Title,
  OverlayContainer,
  OverlayContent,
  RecButton,
} from './styled';

const DEFAULT_RECORD_TIME = 5000;

type Props = {
  activatedCbk: () => void,
  endCbk: (recordedBlob: any) => void,
  mediaNotAllowed: Function,
  videoOptions: VideoOptions,
  title: string,
  svgoverlay?: string,
  speechRecognition: boolean,
  autoStartOnVoiceCommand: boolean,
  recordingTime?: number,
};

type State = {
  mediaActivated: boolean,
  recording: boolean,
  prgsBarValue: number,
  speechRecognised: boolean,
  overlaySize: { width: number, height: number, correction: number },
};

export class MediaCapture extends React.Component<Props, State> {
  state = {
    mediaActivated: false,
    recording: false,
    prgsBarValue: 0,
    speechRecognised: false,
    overlaySize: { width: 300, height: 300, correction: 0 },
  };
  static defaultProps = {
    videoOptions: {
      faceDetection: true,
      recordingTime: 5000,
      speechRecognition: false,
      mediaRecorder: true,
      constrains: { audio: false, video: true },
      imageType: 'image/jpeg',
      fps: 20,
      quality: 0.5,
      overlayAutoRotation: false,
    },
  };
  video: ?HTMLVideoElement;
  pgsBarInterval: IntervalID;
  captureInterval: IntervalID;
  canvasDraw: ?HTMLCanvasElement;
  canvasCapture: ?HTMLCanvasElement;
  htracker: {};
  speechRecognition: {};
  overlayContext: any;

  constructor(props: Props) {
    super(props);
    this.initSpeechSystem();
    if (props.videoOptions.faceDetection) {
      this.htracker = new HT.Tracker({
        faceDetection: props.videoOptions.faceDetection,
        calcAngles: props.videoOptions.faceAngleDetection,
        headPosition: true,
      });
    }
  }

  componentWillUnmount() {
    if (this.video && this.video.srcObject) {
      const tracks = this.video.srcObject.getTracks();
      tracks.forEach(track => track.stop());
    }
    if (this.speechRecognition) {
      this.speechRecognition.abort();
    }
    window.onresize = null;
    this.stopFaceDetection();
    clearInterval(this.pgsBarInterval);
    toggleFullScreen();
  }

  componentDidMount() {
    toggleFullScreen();
    this.overlayContext = this.canvasDraw.getContext('2d');
    if (navigator.mediaDevices) {
      navigator.mediaDevices
        .getUserMedia(this.props.videoOptions.constrains)
        .then((stream: CanvasCaptureMediaStream) => {
          this.video.srcObject = stream;
          this.props.mediaNotAllowed(false);
        })
        .then(this.setCanvasSizes)
        .then(this.props.activatedCbk)
        .then(() => this.setState({ mediaActivated: true }))
        .catch((error: any) => {
          this.props.mediaNotAllowed(true, error);
          log('no camera', error);
        });
    }
    this.startFaceDetection();
  }

  initSpeechSystem = () => {
    if (!(this.props.autoStartOnVoiceCommand || this.props.speechRecognition))
      return;
    this.speechRecognition = Annyang();
    const startCmd = { start: () => this.recordMedia() };
    const speechRecogCmd = {
      'I want to open a BNZ account': () =>
        this.setState({ speechRecognised: true }),
    };
    if (this.props.autoStartOnVoiceCommand) {
      this.speechRecognition.addCommands(startCmd);
    }
    if (this.props.speechRecognition) {
      this.speechRecognition.addCommands(speechRecogCmd);
    }
    this.speechRecognition.start();
  };

  startFaceDetection = () => {
    if (this.props.videoOptions.faceDetection) {
      this.htracker.init(this.video, this.canvasCapture);
      this.htracker.start();
      document.addEventListener('facetrackingEvent', this.faceTrackPaint);
      // document.addEventListener('headtrackrStatus', this.trackStatus);
      this.canvasDraw.style.display = 'inherit';
    }
  };

  stopFaceDetection = () => {
    if (this.props.videoOptions.faceDetection && this.htracker) {
      this.canvasDraw.style.display = 'none';
      document.removeEventListener('facetrackingEvent', this.faceTrackPaint);
      // document.removeEventListener('headtrackrStatus', this.trackStatus);
      this.htracker.stop();
    }
  };

  faceTrackPaint = event => {
    const width = this.video.videoWidth;
    const height = this.video.videoHeight;
    this.overlayContext.clearRect(0, 0, width, height);
    if (event.detection == 'CS') {
      this.overlayContext.translate(event.x, event.y);
      this.overlayContext.rotate(event.angle - Math.PI / 2);
      this.overlayContext.strokeStyle = '#00FF00';
      this.overlayContext.strokeRect(
        -(event.width / 2) >> 0,
        -(event.height / 2) >> 0,
        event.width,
        event.height
      );
      this.overlayContext.rotate(Math.PI / 2 - event.angle);
      this.overlayContext.translate(-event.x, -event.y);
    }
  };

  // trackStatus = event => {
  //   log(event.status);
  // };

  // Set same size as video for both canvas
  setCanvasSizes = () => {
    const video = this.video || {};
    if (video) {
      video.onloadedmetadata = () => {
        const { videoHeight = 0, videoWidth = 0 } = video;
        this.canvasCapture.height = videoHeight;
        this.canvasCapture.width = videoWidth;
        this.canvasDraw.height = videoHeight;
        this.canvasDraw.width = videoWidth;
        this.calcOverlaySize();
        window.onresize = debounce(this.calcOverlaySize, 100);
      };
    }
  };

  // Calc the real video frame size
  calcOverlaySize = () => {
    let correction = 0;
    const val1 = 150; // Rec button X 2
    const val2 = 25; // Title
    const { videoWidth, videoHeight, clientWidth, clientHeight } =
      this.video || {};
    let width = clientWidth;
    let height = clientHeight;
    if (videoHeight / videoWidth > clientHeight / clientWidth) {
      width = videoWidth * clientHeight / videoHeight;
    } else {
      height = videoHeight * clientWidth / videoWidth;
    }
    if (height > clientHeight - val1) {
      correction = (height - (clientHeight - val1)) / 1.5;
      height = height - correction;
    }
    correction = correction > 40 ? 40 : correction;
    this.setState({
      overlaySize: { width, height, correction: correction - val2 },
    });
  };

  recordMedia = () => {
    let data = [];
    this.stopFaceDetection();
    const recorder = Recorder(
      this.video,
      this.canvasCapture,
      this.props.videoOptions
    );
    recorder.ondataavailable = event => (data = event.data);
    recorder.start();
    recorder.onstop = () => this.props.endCbk(data);
    recorder.onerror = event => log(event.name);
    this.triggerProgressBar(() => recorder.stop());
    this.setState({ recording: true });
  };

  triggerProgressBar = (cb: Function): void => {
    const recTime = this.props.recordingTime || DEFAULT_RECORD_TIME;
    this.pgsBarInterval = setInterval(() => {
      const prgsBarValue = this.state.prgsBarValue;
      if (this.state.prgsBarValue > 100) {
        clearInterval(this.pgsBarInterval);
        cb();
      } else {
        this.setState({ prgsBarValue: prgsBarValue + 1 });
      }
    }, recTime / 100);
  };

  render = () => {
    const {
      title,
      svgoverlay,
      videoOptions: { overlayAutoRotation },
    } = this.props;
    const { overlaySize, speechRecognised } = this.state;
    return (
      <Container mediaActivated={this.state.mediaActivated}>
        <VideoContainer>
          <Title speechRecognised={speechRecognised}>{title}</Title>
          <video
            autoPlay
            style={{
              width: '100%',
              height: '100%',
              maxHeight: '100%',
            }}
            ref={video => {
              this.video = video;
            }}
          />
          {svgoverlay ? (
            <OverlayContainer>
              <OverlayContent
                rotate={overlayAutoRotation.toString()}
                overlaysize={overlaySize}
              >
                <IdOverlay svgoverlay={svgoverlay} />
              </OverlayContent>
            </OverlayContainer>
          ) : null}
          <RecButton
            onClick={this.recordMedia}
            recording={this.state.recording}
          >
            <ProgressBar
              style="circular"
              value={this.state.prgsBarValue}
              border={20}
              color="red"
              background="black"
              bgOpacity={0.2}
            />
          </RecButton>
          <canvas
            ref={canvas => {
              this.canvasDraw = canvas;
            }}
            style={{
              position: 'absolute',
              top: '0px',
              left: '0px',
              width: '100%',
              height: '100%',
              display: 'none',
            }}
          />
        </VideoContainer>
        <canvas
          ref={canvas => {
            this.canvasCapture = canvas;
          }}
          style={{
            visibility: 'hidden',
          }}
        />
      </Container>
    );
  };
}
