// @flow

import * as React from 'react';
import { connect } from 'react-redux';
import { Router, Route, Switch } from 'react-router-dom';
import { history } from 'util/history';

import routes from './routes';
import * as appActions from 'app/redux/actions/app';
import { setApiErrorHandlerCbk } from 'util/api-interface';

// Layout
import ScrollToTop from './ScrollToTop';
import Footer from './layout/Footer';
import Header from './layout/Header';
import MobilePanel from './layout/MobilePanel';
import { Wrapper, Container, PanelMask } from './styled';

setApiErrorHandlerCbk(msg => {
  log(msg);
});

type Props = {
  mobilePanel: boolean,
  mobilePanelSwitch: Function,
};

class App extends React.Component<Props> {
  static defaultProps = {
    mobilePanel: false,
  };
  render() {
    const { mobilePanel } = this.props;
    return (
      <Router history={history}>
        <ScrollToTop>
          <Wrapper>
            <Header {...this.props} />
            <Container>
              <Switch>
                {routes.map((route, idx) => <Route key={idx} {...route} />)}
              </Switch>
            </Container>
            <Footer />
          </Wrapper>
          <MobilePanel {...this.props} />
          <PanelMask active={mobilePanel} />
        </ScrollToTop>
      </Router>
    );
  }
}

const mapStateToProps = ({
  app: {
    ui: { mobilePanel, activeURL },
  },
}) => ({
  mobilePanel,
  activeURL,
});

export default connect(mapStateToProps, {
  ...appActions,
})(App);
