// @flow

import { uuidV1 } from 'util/helpers';
import {
  FACE_DETECTION,
  SAVE_VIDEO,
  INIT_BIOMETRIC_ID_SESSION,
  SWITCH_SETTING,
  SET_SETTING,
} from './constants';
import { BIO_ID_FACE_DETECTION, BIO_ID_SAVE_VIDEO } from 'web-api';
import API from 'util/api-interface';

export const faceDetection = ({ data }: any) => dispatch =>
  API(BIO_ID_FACE_DETECTION, 'coordinates', {}, data).then(
    json => dispatch({ type: FACE_DETECTION, json: json.data }),
    err => {
      throw new Error(err);
    }
  );

type SaveVideo = {
  sessionId: string,
  stepNumber: number,
  videoData: Array<any> | Blob,
  videoOptions: VideoOptions,
};
export const saveVideo = ({
  sessionId,
  stepNumber,
  videoData,
  videoOptions,
}: SaveVideo) => (dispatch: Function) =>
  API(
    BIO_ID_SAVE_VIDEO,
    '',
    {},
    { sessionId, stepNumber, videoData, videoOptions }
  ).then(
    json => dispatch({ type: SAVE_VIDEO, json }),
    err => {
      throw new Error(err);
    }
  );

export const initBiometricIdSession = () => ({
  type: INIT_BIOMETRIC_ID_SESSION,
  id: uuidV1(),
});

export const switchSetting = (setting: string) => ({
  type: SWITCH_SETTING,
  setting,
});

export const setSetting = (setting: string, value: string) => ({
  type: SET_SETTING,
  setting,
  value,
});
