// @flow

import * as React from 'react';

import { history } from 'util/history';
import Button from 'components/Buttons';
import Icon from 'components/Icons';
import ProgressBar from 'components/ProgressBar';
import {
  GreenCircle,
  StepContainer,
  MediaNotAllowed,
  StepCounter,
  RedoLink,
  StepTitle,
  StepBodyText,
} from './styled';
import { MediaCapture } from '../MediaCapture';

const START = 'START';
const ACTIVATING = 'ACTIVATING';
const ACTIVE = 'ACTIVE';
const CAPTURING = 'CAPTURING';
const COMPLETED = 'COMPLETED';
const StatusTxt = {
  [START]: 'Start',
  [ACTIVATING]: 'Activating camera',
  [ACTIVE]: 'Camera active',
  [CAPTURING]: 'Capturing',
  [COMPLETED]: 'Next',
};

type Props = {
  videoOptions: VideoOptions,
  saveVideo: Function,
  mediaCaptureTxt: string,
  CallToAction: React.ComponentType<{ status: string }>,
  stepTitleTxt: string,
  stepBodyTxt: string,
  completedTxt: string,
  nextRoute: string,
  redoTxt: string,
  stepNumber: number,
  progressBarInitialValue: number,
  progressBarStepValue: number,
  svgOverlay?: string,
  speechRecognition: boolean,
  autoStartOnVoiceCommand: boolean,
  recordingTime?: number,
  sessionId: string,
  saveVideo: Function,
};
type State = {
  status: string,
  progressBarValue: number,
  MediaNotAllowed: boolean,
};

class Step extends React.Component<Props, State> {
  state = {
    status: START,
    progressBarValue: this.props.progressBarInitialValue,
    MediaNotAllowed: false,
  };
  dnlButton: any;

  constructor(props: Props) {
    super(props);
  }

  setStatus = (status: string) => this.setState({ status });
  setStatusActivatingMedia = () => this.setStatus(ACTIVATING);
  setStatusActivatedMedia = () => this.setStatus(ACTIVE);
  setStatusCaturing = () => this.setStatus(CAPTURING);
  setStatusComplete = (recordedData: any) => {
    this.setStatus(COMPLETED);
    setTimeout(
      () =>
        this.setState({ progressBarValue: this.props.progressBarStepValue }),
      100
    );
    if (recordedData instanceof Blob) {
      // if recordedData is a Blob extract it
      // and send as a Webm video from MediaRecorder
      const reader = new FileReader();
      reader.addEventListener('loadend', () => {
        this.sendVideoData(reader.result);
        this.dnlButton.href = recordedData;
        this.dnlButton.download = 'RecordedVideo.webm';
      });
      reader.readAsDataURL(recordedData);
    } else {
      this.sendVideoData(recordedData);
    }
  };

  sendVideoData = (videoData: Array<any> | Blob) => {
    const { saveVideo, sessionId, stepNumber, videoOptions } = this.props;
    if (this.props.videoOptions.recordEnabled) {
      saveVideo({
        sessionId,
        stepNumber,
        videoData,
        videoOptions,
      });
    } else {
      log('Recording disabled!');
    }
  };

  setMediaNotAllowed = (value: boolean) => {
    this.setState({ MediaNotAllowed: value });
    if (value) {
      this.setStatus(START);
    }
  };

  handleClick = () => {
    if (this.state.status === START) {
      this.setStatusActivatingMedia();
    } else if (this.state.status === COMPLETED) {
      history.push({
        pathname: this.props.nextRoute,
      });
    }
  };

  render = () => {
    const { status, progressBarValue } = this.state;
    const {
      videoOptions,
      mediaCaptureTxt,
      CallToAction,
      stepTitleTxt,
      stepBodyTxt,
      stepNumber,
      progressBarInitialValue,
      completedTxt,
      redoTxt,
      svgOverlay,
      speechRecognition,
      autoStartOnVoiceCommand,
      recordingTime,
    } = this.props;
    return (
      <StepContainer>
        <CallToAction status={status}>
          {status === COMPLETED
            ? [
                <GreenCircle key="1">&#10003;</GreenCircle>,
                <div key="2">{completedTxt}</div>,
                <a
                  key="3"
                  ref={dnlButton => {
                    this.dnlButton = dnlButton;
                  }}
                >
                  <Icon icon="download" size="15" color="#007DC2" />
                </a>,
              ]
            : null}
        </CallToAction>
        <ProgressBar
          value={progressBarValue}
          border={2}
          color="#007DC2"
          background="#007cc3"
          bgOpacity={0.2}
          timeTransform={1.5}
        />
        <StepCounter>
          Step {stepNumber} of 3 {status === COMPLETED ? 'Completed' : null}
        </StepCounter>
        <Body
          status={status}
          stepTitleTxt={stepTitleTxt}
          stepBodyTxt={stepBodyTxt}
        />
        <MediaNotAllowed on={this.state.MediaNotAllowed}>
          We require you to allow us access!
        </MediaNotAllowed>
        <Button width="100%" onClick={this.handleClick}>
          {StatusTxt[status]}
        </Button>
        {[ACTIVATING, ACTIVE, CAPTURING].includes(status) ? (
          <MediaCapture
            videoOptions={videoOptions}
            activatedCbk={this.setStatusActivatedMedia}
            mediaNotAllowed={this.setMediaNotAllowed}
            endCbk={this.setStatusComplete}
            title={mediaCaptureTxt}
            svgoverlay={svgOverlay}
            speechRecognition={speechRecognition}
            autoStartOnVoiceCommand={autoStartOnVoiceCommand}
            recordingTime={recordingTime}
          />
        ) : null}
        {status === COMPLETED ? (
          <RedoLink
            onClick={() =>
              this.setState({
                status: START,
                progressBarValue: progressBarInitialValue,
              })
            }
          >
            {redoTxt}
          </RedoLink>
        ) : null}
      </StepContainer>
    );
  };
}

type BodyProps = {
  status: string,
  stepTitleTxt: string,
  stepBodyTxt: string,
};

const Body = ({ status, stepTitleTxt, stepBodyTxt }: BodyProps) =>
  status !== COMPLETED ? (
    <span>
      <StepTitle>{stepTitleTxt}</StepTitle>
      <StepBodyText>{stepBodyTxt}</StepBodyText>
    </span>
  ) : null;

export default Step;
